import React from 'react'

const StudiesExpandedOne = () => {
    return (
        <div className="studies__section1">
            <div className="studies__expanded">
                <div className="expanded__title title__border">1. Fizyka po godzinach</div>
                <p className="expanded__text">
                    W ramach fizyki po godzinach uczniowie bd pracowali na zajciach w oparciu o podstaw programow nauczania fizyki w szkole, ale nie bd to zajcia, na ktrych realizujemy temat po temacie z podrcznika.
                    W modszej grupie bd to gowie zajcia majce pomc zrozumie na poziomie intuicyjnym omawiane dziay fizyki, a dodatkowo wprowadzony zostanie aparat matematyczny na poziomie odpowiednim dla modego wieku uczniw, ale rwnoczenie pozwalajcy samodzielnie rozwizywa zadania z fizyki i uczy przy tym logicznego mylenia. 
                    W grupie starszej zajcia pocztkowo bd skupiay si na zrozumieniu odmawianych dziaw fizyki, a nastpnie bd ukierunkowane na rozwizywanie zada z treci o rnym stopniu trudnoci, tak aby docelowo pomc w przygotowaniu  modziey do matury z fizyki na poziomie podstawowym i rozszerzonym.
                    <br></br>
                    <br></br>
                    W ramach Fizyki po godzinach uczestnicy bd mieli spotkania z <bold>dr hab. prof. UM w Gdyni Bogusawem Pranszke</bold>, ktre podzielone bd na zajcia lekcyjne oraz na wykady popularnonaukowe. Podczas zaj uczniowie bd poszerzali wiedz zdobyt podczas zaj podstawowych, gwnie poprzez rozwizywanie bardziej zoonych problemw fizycznych pod okiem prowadzcego. W razie potrzeby moliwe jest rwnie wprowadzanie na zajciach bardziej zoonego aparatu matematycznego, ktry uatwi szersze zrozumienie zagadnienia. 
                    Wykady popularnonaukowe bd mi odskoczni, od znojw codziennej szkolnej fizyki. Opiera si one bd gwnie na zagadnieniach zwizanych z kosmosem i grawitacj. Nie wymagaj one umiejtnoci matematycznych, ale pozwalaj szeroko spojrze na omawiane zagadnienia oraz zrozumie rys historyczny, ktry doprowadzi do poszczeglnych odkry. 
                    <br></br>
                    <br></br>
                    Ponadto uczestnicy bd mieli wykady z fizyki z <bold>dr Grzegorzem Gondek</bold>, ktre prowadzone bd po angielsku. Wykady bd dotyczyy zagadnie dobrze znanych modziey, a ich celem bdzie zaznajomienie si i oswojenie z jzykiem angielskim wykorzystywanym w fizyce. 
                    <br></br>
                    <br></br>
                    Dodatkowo zorganizowane bd wyjazdy na Uniwersytet Morski w Gdyni, gdzie modzie bdzie uczestniczya zarwno w zajciach teoretycznych prowadzonych przez pracownikw tej uczelni jak i w zajciach dowiadczalnych. 

                </p>
                <div className="expanded__cross">
                        <span className='cross__cross1'></span>
                        <span className='cross__cross2'></span>
                </div>
            </div>

        </div>
    );
}

export default StudiesExpandedOne;