import React from 'react';

import IRimg1 from '../assets/img/ir1.jpg'
import IRimg2 from '../assets/img/ir2.jpg'
import IRimg3 from '../assets/img/ir3.jpg'

import NZimg1 from '../assets/img/nz1.jpg'
import NZimg2 from '../assets/img/nz2.jpg'
import NZimg3 from '../assets/img/nz3.jpg'

import BPimg1 from '../assets/img/bp1.jpg'
import BPimg2 from '../assets/img/bp2.jpg'
import BPimg3 from '../assets/img/bp3.jpg'

import ASimg1 from '../assets/img/as1.jpg'
import ASimg2 from '../assets/img/as2.jpg'
import ASimg3 from '../assets/img/as3.jpg'

import WMimg1 from '../assets/img/wm1.jpg'
import WMimg2 from '../assets/img/wm2.jpg'
import WMimg3 from '../assets/img/wm3.jpg'

import CZ1img from '../assets/img/cz1.jpg'
import CZ2img from '../assets/img/cz2.jpg'
import CZ3img from '../assets/img/cz3.jpg'

const TeamPerson = () => {
    const teamPersons = [
        {
            name: 'Ignacy Rejmak',
            img1: IRimg1,
            img2: IRimg2,
            img3: IRimg3,
            text: '- nauczyciel fizyki w technikum w Wejherowie i w liceum w Lborku. Przez 10 lat pracowa w gimnazjum uczc matematyki i fizyki. Przez ten czas pracowa z uczniami szczeglnie uzdolnionymi w kierunku przedmiotw cisych, przygotowujc ich do konkursw i olimpiad zarwno fizycznych, matematycznych i informatycznych. Przez ten czas wychowa ponad 20 laureatw i finalistw, gwnie konkursw fizycznych. Od 9 lat wsporganizuje obozy naukowe. Wsppracuje z Uniwersytetem Gdaskim, wsporganizujc konferencje metodyczne dla nauczycieli fizyki z wojewdztwa pomorskiego. Przez swoje niekonwencjonalne metody nauczania, niezwyk osobowo a take relacje z modzie, czsto porwnywany do bohatera ksiki Stowarzyszenie Umarych Poetw. Twierdzi, e ma wicej szczcia ni rozumu, gdy w szkoach w ktrych pracuje zawsze trafia na nauczycieli, od ktrych cigle moe si bardzo wiele nauczy. W Osadzie Wiedzy prowadzi zajcia z fizyki.'
        },
        {
            name: 'Natalia Zaraska',
            img1: NZimg1,
            img2: NZimg2,
            img3: NZimg3,
            text: '- pedagog, coach, doradca zawodowy, psycholog pozytywny. Jej pasj jest zmiana. Jest przekonana, e ludzkie granice nie istniej, a jedyne co nas ogranicza to brak dostatecznej wiedzy do zdobycia umiejtnoci, ktre pozwol nam je przekracza. Wsppracuje z wieloma organizacjami i instytucjami w kraju realizujc projekty z modzie. Jako facylitator Cob Craftingu od lat craftuje swoj prac tworzc musicale i spektakle, ktre pojawiy si na takich scenach jak Stadion Narodowy czy Teatr Muzyczny w Gdyni. Jest zakochana w profilaktyce rwieniczej, o ktrej opowiada jako prelegentka podczas rnorodnych konferencji takich jak np. Kongres Praw Obywatelskich. Wyrniona w konkursie im. Ireny Sendlerowej "Za naprawianie wiata", otrzymaa Medal Komisji Edukacji Narodowej. Pisze bloga o rozwoju osobistym https://podrozybiuro.blogspot.com/. W Osadzie Wiedzy prowadzi warsztaty pewnoci siebie i wartoci wasnej, ktre le w jej obszarze zainteresowa naukowych.'
        },
        {
            name: 'Bogusaw Pranszke',
            img1: BPimg1,
            img2: BPimg2,
            img3: BPimg3,
            text: '- dr hab., prof. nadz. Uniwersytetu Morskiego w Gdyni. Pracuje na Wydziale Mechanicznym UMG, w Katedrze Fizyki. Fizyk, specjalno - fizyka dowiadczalna. Pracowa z uczniami szczeglnie uzdolnionymi w kierunku przedmiotw cisych, przygotowujc ich do olimpiad fizycznych. Zainteresowania: nauczanie oraz  popularyzacja fizyki i astronomii, filmy i ksiki sf, gry planszowe oraz, od ponad 15 lat, AoC (gra komputerowa Age of Empires II: The Conquerors). Strona internetowa http://bpranszke.com'
        },
        {
            name: 'Agata Skrzypalik',
            img1: ASimg1,
            img2: ASimg2,
            img3: ASimg3,
            text: '- nauczycielka matematyki w gdaskiej szkole. Absolwentka projektu Wyobrania waniejsza od wiedzy, w ramach ktrego realizowane s obozy naukowe. Jest przekonana, e kadego czowieka mona zainteresowa matematyk. Zaraa swoim entuzjazmem do tej dziedziny nauki. Uwielbia organizowa wszelkiego rodzaju eventy, angauje si we wszystko, co tylko moe. Posiada niezliczone pokady radoci i umiechu, ktre wykorzystuje na kadym kroku swojego ycia. . Kad woln chwil spdzaaby na grskich szlakach. Cigle w podry, zafascynowana piknem wiata i czowieka. W osadzie wiedzy spenia swoje marzenie prowadzc zajcia z matematyki.'
        },
        {
            name: 'Wojciech Malicki',
            img1: WMimg1,
            img2: WMimg2,
            img3: WMimg3,
            text: '- swj pierwszy program komputerowy napisa w wieku 12 lat na komputerze Commodore 64. Od tego czasu z ma przerw uczy si nowych jzykw programowania i nieustannie poszerza swoj wiedz i umiejtnoci, przechodzc od Basica przez Pascala do C++ i Pythona po drodze zahaczajc o JavaScript i VBA. Od dziewiciu lat pracuje jako nauczyciel, nie tylko w szkole, ale take w projektach zwizanych z prowadzeniem zaj dla modziey uzdolnionej w Supsku, Lborku i Kartuzach. Od 2012 roku uczestniczy w obozach naukowych w czasie wakacji. Przygotowywa rwnie uczniw do olimpiad informatycznych, w tym do olimpiady zespoowej. Aby podnie swoje umiejtnoci uczestniczy w warsztatach dla nauczycieli olimpiady informatycznych. Ukoczy studia podyplomowe z zakresu testowania oprogramowania oraz programowania aplikacji internetowych. Najbardziej ceni u siebie zapa do uczenia si nowych rzeczy. W Osadzie wiedzy prowadzi zajcia z programowania.'
        },
        {
            name: 'Cay zesp',
            img1: CZ1img,
            img2: CZ2img,
            img3: CZ3img,
            text: ''
        }
    ];
    return (
        <>
            {
                teamPersons.map(person => {
                    return(
                        <div key={person.name} className="team__section">
                            <div className="team__name">{person.name}</div>
                            <div className="team__imgs">
                                <div className="team__img"><img src={person.img1} alt=""/></div>
                                <div className="team__img"><img src={person.img2} alt=""/></div>
                                <div className="team__img"><img src={person.img3} alt=""/></div>
                            </div>
                            <div className="team__text">
                                <p>
                                    {person.text}
                                </p>
                            </div>
                        </div>
                    )
                })
            }
        </>
    );
}

export default TeamPerson;