import React from 'react'

const About = () => {
    return (
        <div className='about'>
            <h2 className='about__header header'>Przeczytaj o nas:</h2>
            <p className='about__text'>
                Jest rok 2020. Cay wiat zdominowany zosta przez Mocarstwo Edukacyjnej Negacji. Cay? Nie. Jedna jedyna osada, prowadzona przez nieugitych mentorw wci stawia opr najedcom i walczy dzielnie rzucajc magicznym pyem wyobrani i pasji, aby wrzuceni w otcha testw, sprawdzianw, ocen i schematw modzi mistrzowie stali si sojusznikami umysu. Osada wiedzy przyciga swym blaskiem wszystkich zafascynowanych naukami cisymi wojownikw, aby mogli oni zdobywa skarby bdce ich orem w drodze do sukcesu. Zalepieni wiar w nauk porzucaj letnie, bogie lenistwo i ruszaj na podbj dyscyplin matematyki, fizyki i programowania. W atmosferze rodzinnych relacji rozpoczynaj kady dzie od wiczenia tyzny fizycznej, aby nastpnie poruszy bystry umys do zdobywania wszelkiej mdroci. Wyposaeni w pewno siebie i odwag wyruszaj z osady z wiar w sukces, przekonani, e wyobrania nie zna granic i zawsze jest waniejsza od wiedzy, lecz wiedzc jednoczenie, e wiedza jest moc a moc jest wiedz. Osada wiedzy jest ich sojusznikiem a oni s sojusznikami zdominowanych przez Mocarstwo dajc im wiato na lepsze czasy.
            </p>
            <div className="about__header header">Obejrzyj</div>
            <iframe title='movie' className='frame' src="https://www.youtube.com/embed/rYot1-OtgTw" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>

        </div>
    );
}

export default About;